import { Component, OnInit } from '@angular/core';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { GaUntukService, GaBiayaService, GaBiayaRincianService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Biaya, Biaya_rincian, Pegawai_master, Untuk } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private gaUntukService: GaUntukService,
        private gaBiayaService: GaBiayaService,
        private gaBiayaRincianService: GaBiayaRincianService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) { }

    dataBiaya: Biaya = {}
    dataRincian: Biaya_rincian[] = []
    id_biaya: string = ''
    id_pegawai: string = ''

    listPegawai: Pegawai_master[] = []
    listUntuk: Untuk[] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPreeData()
        this.form()
        this.beforeCreate()
    }
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.gaUntukService.getGaUntuks().subscribe(data => {
            this.listUntuk = data.data
        })
    }


    keperluan: any = ''
    nilai: any = ''
    tambahRincian() {
        if (this.keperluan == '') {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.gaBiayaRincianService
            .postGaBiayaRincians({
                id_biaya: this.id_biaya,
                keperluan: this.keperluan,
                nilai: this.nilai,
            })
            .subscribe(res => {
                this.getRincian()
            })
    }

    listRincian: any[] = []
    getRincian() {
        let filter = {
            id_biaya: this.id_biaya,
        }
        this.gaBiayaRincianService.getGaBiayaRincians(JSON.stringify(filter)).subscribe(res => {
            this.listRincian = res.data
        })
    }
    deleteRincian(id) {
        this.spinner.show()
        this.gaBiayaRincianService.deleteGaBiayaRinciansId(id).subscribe(data => {
            this.getRincian()
            this.spinner.hide()
        })
    }


    beforeCreate() {
        this.gaBiayaService.postGaBiayas(this.dataBiaya).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.id_biaya = data.data.id_biaya
                console.log(data.data.id_biaya)
                this.getRincian()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    dataForm: FormGroupTypeSafe<Biaya>
    form() {
        this.dataForm = this.fb.group<Biaya>({
            id_pegawai: new FormControl(this.id_pegawai),
            id_biaya: new FormControl(''),
            tanggal_request: new FormControl(''),
            id_untuk: new FormControl(''),
            keterangan: new FormControl(''),
            total: new FormControl(''),
            id_document: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            status: new FormControl(''),
            nomor: new FormControl(''),
            posisi: new FormControl(''),
            id_dokumen: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataForm.patchValue({id_pegawai: this.id_pegawai})

        this.dataBiaya = this.dataForm.value
        this.gaBiayaService.putGaBiayasId({ id: this.id_biaya, body: this.dataBiaya }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/profile/expenses/list'])
    }


}
